import React from "react";
import axios from "axios";

const config = {
  method: "get",
  url: "https://api.github.com/search/users",
  params: {
    q: ""
  }
};

export default class Github extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "",
      data: [],
      isLoading: true,
      error: false,
      errorMessage: ""
    };
  }
  getData = (query) => {
    this.setState({
      isLoading: true,
      error: false
    });
    config.params.q = query;
    console.log(config);
    axios(config)
      .then((res) =>
        this.setState({
          data: res.data.items,
          isLoading: false,
          error: false
        })
      )
      .catch((err) => {
        this.setState({
          error: true,
          errorMessage: err,
          isLoading: false
        });
      });
  };
  componentDidMount() {
    this.getData("dhanasekhar");
  }
  render() {
    const { isLoading, data, query, error, errorMessage } = this.state;
    console.log(errorMessage);
    return (
      <div>
        <input
          value={query}
          placeholder="search query"
          onChange={(e) => this.setState({ query: e.target.value })}
        />
        <button onClick={() => this.getData(query)}> SEARCH </button>
        {isLoading && <div> Loading ... </div>}
        {!isLoading && error && <div>Something went wrong</div>}
        <div>
          {!isLoading &&
            !error &&
            data &&
            data.map((item) => (
              <div key={item.id}>
                <div>
                  <img
                    src={item.avatar_url}
                    style={{ width: "100px", borderRadius: "50px" }}
                    alt={item.login}
                  />
                  <p>{item.login}</p>
                </div>
              </div>
            ))}
        </div>
      </div>
    );
  }
}
